import React from 'react'
import './zoom-b.css'
// import './zoom-D.js'

export default function index(props) {
  const { name, type, image } = props.work
  return (
    <div className="flex flex-col pb-2 bg-gray-700 rounded-lg shadow-lg zoomBIn" >
      <img className="object-contain w-full rounded-t-lg" src={image} alt="" />

      <div className="flex flex-row">
        <div className="flex flex-col w-full px-4 py-2 md:w-6/12">
          <h2 className="mb-2 text-base text-white md:text-base">{name}</h2>
          <svg className="justify-center w-12 mb-2 align-middle " viewBox="0 0 48 3">
            <rect width="48" height="2" fill="#bcc0c4" />
          </svg>
          <p className="text-sm text-gray-500 md:text-sm">{type}</p>
        </div>
        <div className="flex items-end justify-end w-6/12 px-4 py-2">
          <p className="hidden text-sm text-gray-500 md:text-xs">View more</p>
        </div>
      </div>
    </div>
  )
}
